}

.dataTables_info {
    float: left;
    font-size: 11px;
    padding: 8px 10px;
    line-height: 16px;
}

.dataTables_paginate {
    padding: 5px 5px;
    text-align: right;
    float: right;
}

.dataTables_paginate a.paginate_disabled_previous, .dataTables_paginate a.paginate_disabled_next, .dataTables_paginate a.paginate_button, .paginate_enabled_next, .paginate_active, .paginate_enabled_previous {
    padding: 3px 5px;
    margin-left: 3px;
    font-size: 11px;
    background: rgba(0, 0, 0, 0.2);
}

.paginate_active {
    background: rgba(0, 0, 0, 0.5);
}

.dataTables_paginate .paginate_enabled_next:hover, .dataTables_paginate a.paginate_button:hover, .dataTables_paginate .paginate_enabled_previous:hover {
    background: rgba(0, 0, 0, 0.4);
    cursor: pointer;
}

.dataTables_paginate a.paginate_disabled_previous, .dataTables_paginate a.paginate_button_disabled, .dataTables_paginate a.paginate_disabled_next {
    cursor: default;
    color: #ccc;
}

.dataTables_paginate a.paginate_button_disabled:hover {
    box-shadow: none;
    color: #ccc;
}

.sorting_desc, .sorting_asc, .sorting {
    position: relative;
    cursor: pointer;
}

.sorting:before, .sorting_desc:before, .sorting_asc:before {
    position: absolute;
    right: 5px;
    font-family: 'FontAwesome';
    opacity: 0.9;
    filter: alpha(opacity=90);
}

.sorting:before {
    content: "\f0dc";
    opacity: 0.3;
    filter: alpha(opacity=30);
}

.sorting_desc:before {
    content: "\f0d8";
}

.sorting_asc:before {
    content: "\f0d7";
}

.sorting_asc_disabled:before {
    opacity: 0.1;
    filter: alpha(opacity=10);
}

.sorting_desc_disabled:before {
    opacity: 0.1;
    filter: alpha(opacity=10);
}

/* eof datatables */

/* ibutton */
.ibutton-container {
    position: relative;
    height: 30px;
    width: 60px;
    line-height: 0px;
    cursor: pointer;
    overflow: hidden;
    max-width: 200px;
    -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -moz-user-focus: ignore;
    -moz-user-input: disabled;
    background: rgba(0, 0, 0, 0.3);
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
    display: inline-block;
}

.ibutton-container input {
    position: absolute;
    top: 0;
    left: 0;
    filter: alpha(opacity=0);
    -moz-opacity: 0.0;
    opacity: 0.0;
    -moz-user-input: enabled !important;
}

.ibutton-handle {
    height: 30px;
    width: 30px;
    cursor: inherit;
    position: absolute;
    top: 0px;
    left: 0px;
    background: #FFF;
    z-index: 3;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
}

div.ibutton-label-on,
div.ibutton-label-off {
    white-space: nowrap;
    display: block;
    height: 30px;
    position: absolute;
    top: 0;
    overflow: hidden;
}

div.ibutton-label-on span,
div.ibutton-label-off span {
    display: none;
}

div.ibutton-label-on {
    background: #59AD2F;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
}

div.ibutton-label-off {
    background: #AF2F2F;
    width: 100%;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
}

.ibutton-padding-left {
    left: 0;
}

.ibutton-padding-right {
    right: 0;
}

.ibutton-disabled {
    cursor: not-allowed !important;
    opacity: 0.6;
    filter: alpha(opacity=60);
}

/* eof ibutton */

